import React, { useContext, useState, useEffect } from "react";

import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import "../css/MyPageCss.css";
import MyPageContext from "../MyPageContext";

const MyPage = () => {
  const { loginMember, setLoginMember } = useContext(MyPageContext);
  const [memberInfo, setMemberInfo] = useState({
    memberId: "",
    memberPw: "",
    memberEmail: "",
    memberName: "",
    memberPhone: "",
    memberAddress: "",
    memberProfile: "",
  });
  console.log(loginMember);
  const [defaultName, setDefaultName] = useState("");
  const [profileImage, setProfileImage] = useState(null);
  const [profileImagePreview, setProfileImagePreview] = useState("");

  //   
  const [idDefindMessage, setidDefindMessage] = useState("");
  const [pwDefindMessage, setpwDefindMessage] = useState("");
  const [phoneDefindMessage, setphoneDefindMessage] = useState("");
  const [nameDefindMessage, setnameDefindMessage] = useState(""); //  

  const mainNavigate = useNavigate();

  useEffect(() => {
    if (loginMember) {
      axios
        .get(`/api/mypage/${loginMember.memberId}`)
        .then((response) => {
          setMemberInfo(response.data);
          setDefaultName(response.data.memberName);
          if (response.data.memberProfile) {
            setProfileImagePreview(
              `/images/userProfile/${response.data.memberProfile}`
            );
          }
        })
        .catch((error) => {
          console.error("   :", error);
        });
    }
  }, [loginMember]);

  //       
  const checkDataInput = (name, value) => {
    const idCheck = /^[a-zA-Z0-9]{4,12}$/;
    const pwCheck = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;
    const phoneCheck = /^\d{10,11}$/;
    const nameCheck = /^[-a-zA-Z\s]{2,30}$/; //  

    switch (name) {
      case "memberId":
        if (!idCheck.test(value)) {
          setidDefindMessage(
            " 4~12     ."
          );
        } else {
          setidDefindMessage("");
        }
        break;
      case "memberPw":
        if (!pwCheck.test(value)) {
          setpwDefindMessage(
            " 8  ,    ."
          );
        } else {
          setpwDefindMessage("");
        }
        break;
      case "memberPhone":
        if (!phoneCheck.test(value)) {
          setphoneDefindMessage(" 10~11   .");
        } else {
          setphoneDefindMessage("");
        }
        break;
      case "memberName":
        if (!nameCheck.test(value)) {
          setnameDefindMessage(
            " 2~30 ,     ."
          );
        } else {
          setnameDefindMessage("");
        }
        break;
      default:
        break;
    }
  };

  const userInfoChange = (e) => {
    const { name, value } = e.target;
    setMemberInfo((prevState) => ({
      ...prevState,
      [name]: value,
    }));
    checkDataInput(name, value); //       
  };

  const profileChange = (e) => {
    const file = e.target.files[0];
    setProfileImage(file);
    if (file) {
      const fileURL = URL.createObjectURL(file);
      setProfileImagePreview(fileURL);
      return () => URL.revokeObjectURL(fileURL);
    }
  };

  const userInfoSubmit = async () => {
    const formData = new FormData();
    formData.append(
      "memberInfo",
      new Blob([JSON.stringify(memberInfo)], { type: "application/json" })
    );
    if (profileImage) {
      formData.append("memberProfile", profileImage);
    }

    if (window.confirm("  ?")) {
      await axios
        .put("/api/mypage/update", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          alert("   ");
          setLoginMember((prevMember) => ({
            ...prevMember,
            ...response.data,
          }));
          mainNavigate("/");
        })
        .catch((error) => {
          console.error("    :", error);
        });
    }
  };

  const userDeleteBtn = () => {
    if (window.confirm("   ?")) {
      axios
        .delete(`/api/mypage/delete/${loginMember.memberNo}`)
        .then(() => {
          alert("  .");
          setLoginMember(null);
          mainNavigate("/");
        })
        .catch((error) => {
          console.error("   :", error);
        });
    }
  };

  if (!loginMember) {
    return <div className="login-prompt"> .</div>;
  }

  return (
    <div className="mypage-container">
      <h1 className="mypage-title">{defaultName} </h1>
      <div className="mypage-content">
        <div className="user-info">
          <label>
            
            <input
              type="text"
              name="memberName"
              value={memberInfo.memberName}
              onChange={userInfoChange}
              className="input-field"
            />
            {nameDefindMessage && (
              <div
                className={`error-message ${
                  nameDefindMessage.includes("") ? "valid" : "invalid"
                }`}
              >
                {nameDefindMessage}
              </div>
            )}
          </label>
          <label>
            
            <input
              type="email"
              name="memberEmail"
              value={memberInfo.memberEmail}
              onChange={userInfoChange}
              className="input-field"
            />
          </label>
          <label>
            
            <input
              type="text"
              name="memberAddress"
              value={memberInfo.memberAddress}
              onChange={userInfoChange}
              className="input-field"
            />
          </label>
          <label>
             
            <input
              type="text"
              name="memberPhone"
              value={memberInfo.memberPhone}
              onChange={userInfoChange}
              className="input-field"
            />
            {phoneDefindMessage && (
              <div
                className={`error-message ${
                  phoneDefindMessage.includes("") ? "valid" : "invalid"
                }`}
              >
                {phoneDefindMessage}
              </div>
            )}
          </label>
        </div>
        <div className="user-profile">
          <label>
             
            <div className="profile-thumbnail">
              {profileImagePreview && (
                <div className="profile-img">
                  <img src={profileImagePreview} className="profile-preview" />
                </div>
              )}
            </div>
            <input
              type="file"
              name="memberProfile"
              accept="image/*"
              onChange={profileChange}
              className="file-input"
            />
          </label>
          <label>
            I D
            <input
              type="text"
              name="memberId"
              value={memberInfo.memberId}
              onChange={userInfoChange}
              className="input-field"
            />
            {idDefindMessage && (
              <div
                className={`error-message ${
                  idDefindMessage.includes("") ? "valid" : "invalid"
                }`}
              >
                {idDefindMessage}
              </div>
            )}
          </label>
          <label>
            P W
            <input
              type="password"
              name="memberPw"
              value={memberInfo.memberPw}
              onChange={userInfoChange}
              className="input-field"
            />
            {pwDefindMessage && (
              <div
                className={`error-message ${
                  pwDefindMessage.includes("") ? "valid" : "invalid"
                }`}
              >
                {pwDefindMessage}
              </div>
            )}
          </label>
        </div>
      </div>

      <div className="button-group">
        <button className="submit-button" onClick={userInfoSubmit}>
          
        </button>
        <button className="delete-button" onClick={userDeleteBtn}>
           
        </button>
        <Link to="/">
          <button className="home-button"> </button>
        </Link>
      </div>
    </div>
  );
};

export default MyPage;
