import React, { useEffect, useState } from "react";
import axios from "axios";
import '../css/GoodsShop.css';
import { Link } from "react-router-dom";

const GoodsShop = () => {
    const [goods, setGoods] = useState([]); 
    

    //  useEffect ->       
    useEffect(() => {
        axios.get('http://localhost:9000/goods/all')
            .then(response => { //   
                setGoods(response.data); //  goods      
            })
            .catch(err => { //  
                console.log("Error: ", err);
            });
    }, []);




  // 
  const addToShoppingBasket = (good) => {
    const basketItem = {
      memberNo: 1, //     
      goodsNo: good.goodsNo,
      goodsQuantity: 1,
      basketTotal: good.goodsPrice
    };
    axios.post('http://localhost:9000/basket/add', basketItem)
      .then(() => {
        alert(" .");
      })
      .catch(err => {
        console.log("Error: ", err);
      });
  };




    // -  
    const soccerShoes = goods.filter(good => good.goodsKind === "");
    const futsalShoes = goods.filter(good => good.goodsKind === "");
    const uniform = goods.filter(good => good.goodsKind === "");



    return (
      <div className='goods-container'>
          <h1></h1>
          <div className='goods-content'>
              {soccerShoes.map(good => {/* process =  env =  PUBLIC_URL =       images =    good.goodsImage =       
                alt=             
                */
                  const imgSrc = `${process.env.PUBLIC_URL}/images/${good.goodsImage}`;
                  return (
                      <div key={good.goodsNo} className='goods'>
                          <Link to={`/goodsDetail/${good.goodsNo}`} state={{ good: good }} className="GoodsDetail-link">
                              <img src={imgSrc} alt={good.goodsImage} />
                              <h2>{good.goodsName}</h2>
                              <p> : {good.goodsKind}</p>
                              <p> : {good.goodsPrice.toLocaleString()}</p>
                              <p> : {good.goodsComment}</p>
                              <p> : {good.goodsSize}</p>
                              <p> : {good.goodsSellNow ? "" : ""}</p>
                              <button className='add-to-basket-btn' onClick={() => addToShoppingBasket(good)}> </button>
                          </Link>
                      </div>
                  );
              })}
          </div>

          <h1></h1>
          <div className='goods-content'>
              {futsalShoes.map(good => {
                  const imgSrc = `${process.env.PUBLIC_URL}/images/${good.goodsImage}`;
                  return (
                      <div key={good.goodsNo} className='goods'>
                          <Link to={`/goodsDetail/${good.goodsNo}`} state={{ good: good }} className="GoodsDetail-link">
                              <img src={imgSrc} alt={good.goodsImage} />
                              <h2>{good.goodsName}</h2>
                              <p> : {good.goodsKind}</p>
                              <p> : {good.goodsPrice.toLocaleString()}</p>
                              <p> : {good.goodsComment}</p>
                              <p> : {good.goodsSize}</p>
                              <p> : {good.goodsSellNow ? "" : ""}</p>
                              <button className='add-to-basket-btn' onClick={() => addToShoppingBasket(good)}> </button>
                          </Link>
                      </div>
                  );
              })}
          </div>

          <h1></h1>
          <div className='goods-content'>
              {uniform.map(good => {
                  const imgSrc = `${process.env.PUBLIC_URL}/images/${good.goodsImage}`;
                  return (
                      <div key={good.goodsNo} className='goods'>
                          <Link to={`/goodsDetail/${good.goodsNo}`} state={{ good: good }} className="GoodsDetail-link">
                              <img src={imgSrc} alt={good.goodsImage} />
                              <h2>{good.goodsName}</h2>
                              <p> : {good.goodsKind}</p>
                              <p> : {good.goodsPrice.toLocaleString()}</p>
                              <p> : {good.goodsComment}</p>
                              <p> : {good.goodsSize}</p>
                              <p> : {good.goodsSellNow ? "" : ""}</p>
                              <button className='add-to-basket-btn' onClick={() => addToShoppingBasket(good)}> </button>
                          </Link>
                      </div>
                  );
              })}
          </div>
      </div>
  );
};

export default GoodsShop;
