import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link, useLocation, useParams } from "react-router-dom";
import StadiumReviewUpload from "./StadiumReviewUpload";

const StadiumDetail = () => {
  const location = useLocation();
  const list = location.state.list;
  const [contentBoxView, setContentBoxView] = useState(false);
  const [reviews, setReviews] = useState([]);
  const [likeSum, setLikeSum] = useState(0);
  const [dislikeSum, setDislikeSum] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const reviewsPerPage = 5;

  const { stadiumNo } = useParams();

  useEffect(() => {
    //  
    axios
      .get(`/api/stadiumReview/${stadiumNo}/likeCount`)
      .then((response) => {
        setLikeSum(response.data);
      })
      .catch((error) => {
        console.error("    .", error);
      });
    //  
    axios
      .get(`/api/stadiumReview/${stadiumNo}/dislikeCount`)
      .then((response) => {
        setDislikeSum(response.data);
      })
      .catch((error) => {
        console.error("    .", error);
      });
    //  
    axios
      .get(`/api/stadiumReview/${stadiumNo}`)
      .then((response) => {
        setReviews(response.data);
      })
      .catch((error) => {
        console.error("  .", error);
      });
  }, [stadiumNo, reviews]);

  const indexOfLastReview = currentPage * reviewsPerPage;
  const indexOfFirstReview = indexOfLastReview - reviewsPerPage;
  const currentReviews = reviews.slice(indexOfFirstReview, indexOfLastReview);

  const pagination = (page) => {
    setCurrentPage(page);
  };

  const pageNumbers = [];
  for (let i = 1; i <= Math.ceil(reviews.length / reviewsPerPage); i++) {
    pageNumbers.push(i);
  }

  return (
    <div className="stadiumdetail-container">
      <div className="stadiumdetail-content">
      <h3 className="headers"> {list.stadiumName}   </h3>
        <img src={list.stadiumImage} alt={list.stadiumName} />
        <p>  : {list.stadiumName}</p>
        <p>   : {list.stadiumCapacity} </p>
        <p> : {list.stadiumAddress}</p>
        <div className="icon-container">
          <label>
            <img src="/icon-like.png" alt="Like Icon" />
            <br />
            {likeSum} <br/>
            
          </label>
          <label>
            <img src="/icon-dislikes.png" alt="Dislike Icon" />
            <br />
            {dislikeSum} <br/>
            
          </label>
        </div>
      </div>
      <div className="stadiumreview">
        <Link to="/stadiumInfo"><button></button></Link>
        <button onClick={() => setContentBoxView(!contentBoxView)}>
          {contentBoxView ? "" : ""}
        </button>
        <div className="contentBoxView-wrapper">
          {contentBoxView && <StadiumReviewUpload />}
        </div>

        {/*     */}
        <div className="reviews">
          <h2 className="headers"></h2>
          {currentReviews.length > 0 ? (
            currentReviews.map((review) => (
              <div key={review.stadiumReviewNo} className="review">
                <p></p>
                <p className="review-comment">
                    : {review.stadiumComment}
                </p>
                <p>  : {review.stadiumCommentDate}</p>
                <p>
                  {review.likeCount > 0 && <span></span>}
                  {review.dislikeCount > 0 && <span></span>}
                </p>
              </div>
            ))
          ) : (
            <p>  .</p>
          )}
          <div className="pagination">
            {pageNumbers.map((number) => (
              <button
                key={number}
                onClick={() => pagination(number)}
                className={currentPage === number ? "active" : ""}
              >
                {number}
              </button>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default StadiumDetail;
