import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import '../css/Modal.css';

const PasswordChange = ({ userInfo }) => {
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const navigate = useNavigate();

  //    
  const passwordValidationRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

  const handlePasswordChange = async () => {
    if (!newPassword || !confirmPassword) {
      setError("  .");
      return;
    }

    if (!passwordValidationRegex.test(newPassword)) {
        setError('  8,      .');
        return;
      }

    if (newPassword !== confirmPassword) {
      setError("  .");
      setSuccess('');
      return;
    }

    try {
      const response = await axios.put('/passwordChange', {
        memberEmail: userInfo.memberEmail,
        memberPw: newPassword,
      });

      if (response.data === '  ') {
        setSuccess("  .");
        setError('');
      } else {
        setError("  .");
      }
    } catch (error) {
      console.error('Axios error:', error);
      setError("    .");
    }
  };

  const handleLoginRedirect = () => {
    navigate('/login'); //   
  };

  return (
    <div>
      <h2> </h2>
      
      {success && <div style={{ color: 'green' }}>{success}</div>}
      {!success ? (
        <div className='password-container'>
          <div>
            <label> :</label>
            <input
              type="password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
            />
          </div>
          <div>
            <label> :</label>
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
          </div>
          {error && <div style={{ color: 'red' }}>{error}</div>}
          <button className='btn btn-dark' onClick={handlePasswordChange}> </button>
        </div>
      ) : (
        <button className='btn btn-success login-page-button' onClick={handleLoginRedirect}>  </button>
      )}
    </div>
  );
};

export default PasswordChange;