import { useState } from "react";
import Carousel from 'react-bootstrap/Carousel';
import '../css/MainPage.css';
import { Link } from "react-router-dom";

const MainPageCarousel = () => {
    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex) => {
        setIndex(selectedIndex);
    };

    return (
        <Carousel activeIndex={index} onSelect={handleSelect} interval={1500}>
            <Carousel.Item className='carousel-items'>
                <Link to="/galleryBoard">
                    <img src="/images/tothegallery.jpg" alt='  1' />
                </Link>
                <Carousel.Caption className='carousel-caption-text'>
                    <h3> </h3>
                    <p>  </p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item className='carousel-items'>
                <Link to="/GoodsShop">
                    <img src="/images/totheshop.jpg" alt='  2' />
                </Link>
                <Carousel.Caption className='carousel-caption-text'>
                    <h3> </h3>
                    <p>  ,  </p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item className='carousel-items'>
                <Link to="/LiveStreamingPage">
                    <img src="/images/tothelive.jpg" alt='  3' />
                </Link>
                <Carousel.Caption className='carousel-caption-text'>
                    <h3>  </h3>
                    <p>      </p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item className='carousel-items'>
                <Link to="/StadiumList">
                    <img src="/images/tothestadiumlist.jpg" alt='  4' />
                </Link>
                <Carousel.Caption className='carousel-caption-text'>
                    <h3> </h3>
                    <p>,      </p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    )
}
export default MainPageCarousel;