import React, { useState } from 'react';
import './css/Signup.css'; // CSS   
import axios from 'axios';

const Signup = () => {
  const [member, setMember] = useState({
    memberId: '',
    memberPw: '',
    memberPwConfirm: '', 
    memberName: '',
    memberPhone: '',
    memberEmail: '',
    memberAddress: '',
    memberBirth: '',
    profileImage: '', //   URL 
    memberProfile: null,  //     
  });

  const [errors, setErrors] = useState({
    memberId: '',
    memberPw: '',
    memberPwConfirm: '', 
    memberName: '',
    memberPhone: '',
    memberEmail: ''
  });

  const [isIdAvailable, setIsIdAvailable] = useState(null); //     

  const handleChange = (e) => {
    const { name, value } = e.target;
    validateField(name, value);

    setMember((prevMember) => ({
      ...prevMember,
      [name]: value
    }));
  };

  const validateField = (name, value) => {
    let errorMsg = '';

    switch (name) {
      case 'memberId':
        if (!/^[a-zA-Z0-9]{4,12}$/.test(value)) {
          errorMsg = ' 4-12 ,   .';
        }
        break;
      case 'memberPw':
        if (!/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/.test(value)) {
          errorMsg = '  8,       .';
        }
        break;
      case 'memberPwConfirm':
        if (value !== member.memberPw) {
          errorMsg = '  .';
        }
        break;
      case 'memberName':
        if (!/^[-a-zA-Z\s]{2,30}$/.test(value)) {
          errorMsg = ' 2-30    .';
        }
        break;
      case 'memberPhone':
        if (!/^\d{10,11}$/.test(value)) {
          errorMsg = ' -  10-11  .';
        }
        break;
      case 'memberEmail':
        if (!/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/.test(value)) {
          errorMsg = '   .';
        }
        break;
      default:
        break;
    }

    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: errorMsg,
    }));
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setMember({
        ...member,
        profileImage: URL.createObjectURL(file), //   URL 
        memberProfile: file //     
      });
    }
  };

  const handleIdCheck = async () => {
    if (!member.memberId.trim()) {
      alert(" ");
      setIsIdAvailable(false);
      return;
    }
  
    try {
      const response = await axios.get('http://localhost:9000/api/idCheck', {
        params: { id: member.memberId },
      });
  
      if (response.data.isAvailable) {
        setIsIdAvailable(true);
        setErrors((prevErrors) => ({
          ...prevErrors,
          memberId: '',
        }));
      } else {
        setIsIdAvailable(false);
        setErrors((prevErrors) => ({
          ...prevErrors,
          memberId: '   .',
        }));
      }
    } catch (error) {
      console.error('ID    :', error);
      setIsIdAvailable(false);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    //      
    Object.keys(member).forEach((name) => validateField(name, member[name]));

    //       
    if (!member.memberProfile) {
        setErrors({
            ...errors,
            memberProfile: '  .'
        });
        return;
    }

    //     
    if (Object.values(errors).some((error) => error !== '')) {
      console.log('  :', errors);
      return;
    }

    console.log('Member Data:', member);

    const formData = new FormData();
    formData.append('memberId', member.memberId);
    formData.append('memberPw', member.memberPw);
    formData.append('memberName', member.memberName);
    formData.append('memberPhone', member.memberPhone);
    formData.append('memberEmail', member.memberEmail);
    formData.append('memberAddress', member.memberAddress);
    formData.append('memberBirth', member.memberBirth);
    formData.append('profileImage', member.memberProfile);

    fetch('http://localhost:9000/api/members', {
      method: 'POST',
      body: formData,
    })
      .then((response) => {
        if (response.ok) {
          return response.text(); //     
        } else {
          throw new Error(' ');
        }
      })
      .then((fileName) => {
        console.log(':', fileName);
        alert(' !'); //  
        setMember({
          memberId: '',
          memberPw: '',
          memberPwConfirm: '', 
          memberName: '',
          memberPhone: '',
          memberEmail: '',
          memberAddress: '',
          memberBirth: '',
          profileImage: '', 
          memberProfile: null,
        });
      })
      .catch((error) => {
        console.error(' :', error);
        alert(' !'); //  
      });
  };

  const handleAddressSearch = () => {
    new window.daum.Postcode({
      oncomplete: function(data) {
        setMember((prevMember) => ({
          ...prevMember,
          memberAddress: data.address
        }));
      }
    }).open();
  };

  return (
    <div className="signup-container">
      <form onSubmit={handleSubmit}>
        <div>
          <label>
            
            <div className="input-container">
              <input
                type="text"
                name="memberId"
                value={member.memberId}
                onChange={handleChange}
              />
              <button type="button" onClick={handleIdCheck}></button>
            </div>
            {errors.memberId && (
              <div style={{ color: 'red' }}>{errors.memberId}</div>
            )}
            {isIdAvailable === true && (
              <div style={{ color: 'green' }}>  .</div>
            )}
          </label>
        </div>
        <div>
          <label>
            
            <input
              type="password"
              name="memberPw"
              value={member.memberPw}
              onChange={handleChange}
            />
            {errors.memberPw && (
              <div style={{ color: 'red' }}>{errors.memberPw}</div>
            )}
          </label>
        </div>
        <div>
          <label>
             
            <input
              type="password"
              name="memberPwConfirm"
              value={member.memberPwConfirm}
              onChange={handleChange}
            />
            {errors.memberPwConfirm && (
              <div style={{ color: 'red' }}>{errors.memberPwConfirm}</div>
            )}
          </label>
        </div>
        <div>
          <label>
            
            <input
              type="text"
              name="memberName"
              value={member.memberName}
              onChange={handleChange}
            />
            {errors.memberName && (
              <div style={{ color: 'red' }}>{errors.memberName}</div>
            )}
          </label>
        </div>
        <div>
          <label>
            
            <input
              type="text"
              name="memberPhone"
              value={member.memberPhone}
              onChange={handleChange}
            />
            {errors.memberPhone && (
              <div style={{ color: 'red' }}>{errors.memberPhone}</div>
            )}
          </label>
        </div>
        <div>
          <label>
            
            <input
              type="email"
              name="memberEmail"
              value={member.memberEmail}
              onChange={handleChange}
            />
            {errors.memberEmail && (
              <div style={{ color: 'red' }}>{errors.memberEmail}</div>
            )}
          </label>
        </div>
        <div>
          <label>
            
            <input
              type="date"
              name="memberBirth"
              value={member.memberBirth}
              onChange={handleChange}
            />
          </label>
        </div>
        <div>
          <label>
            
            <div className="input-container">
              <input
                type="text"
                name="memberAddress"
                value={member.memberAddress}
                onChange={handleChange}
              />
              <button type="button" onClick={handleAddressSearch}> </button>
            </div>
          </label>
        </div>
        <div>
          <label>
            
            <input
              type="file"
              accept="image/*"
              onChange={handleImageChange}
            />
          </label>
          {member.profileImage && (
            <div>
              <img
                src={member.profileImage} 
                alt="Profile Preview"
                style={{ width: '100px', height: '100px', marginTop: '10px' }}
              />
            </div>
          )}
        </div>
        <div className="button-container">
          <button type="submit"></button>
        </div>
      </form>
    </div>
  );
};

export default Signup;
