import React, {useState} from "react";
import { useLocation, useNavigate } from "react-router-dom";
import '../css/GoodsDetail.css';
import axios from "axios";

// 
const GoodsDetail = ({ loginMember, userBasketItem, checkLogin }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const good = location.state.good;


  // 
  const returnToGoodsShop = () => {
    navigate("/goodsShop");
  };

  
  //  
  const [selectSize, setSelectSize] = useState(good.goodsSize ? good.goodsSize.split(",")[0] : "");

  //  
  const handleSizeChange = (e) => {
    setSelectSize(e.target.value);
  };
  const sizes = good.goodsSize ? good.goodsSize.split(",") : [];

  //  
  const imgSrc1 = `${process.env.PUBLIC_URL}/images/${good.goodsImage}`;    //1
  console.log(imgSrc1);
  const imgSrc2 = `${process.env.PUBLIC_URL}/images/${good.goodsImage2}`;   //2
  console.log(imgSrc2);
  


  //  
  const addToShoppingBasket = () => {

    //  
    if(!loginMember) { 
      if(window.confirm(" !")){ //window.confirm alert /
        navigate("/login") // 
      }
      return;
    }




    const basketItem = {
      memberNo: loginMember.memberNo,
      goodsNo: good.goodsNo,
      goodsQuantity: 1,
      basketTotal: good.goodsPrice,
      goodsSize: selectSize
    };

    axios.post('http://localhost:9000/basket/add', basketItem)
      .then(() => {
        if(window.confirm(' . ?')) {
          navigate("/shoppingBasket");
        }
      })
      .catch(err => {
        console.error("Error: ", err);
      });
  };


  if (!good) {
    return <p>    .</p>; //    
  }
  return (
    <div className="detail-container">
      <h2>{good.goodsName}</h2>
      <hr />
      <img src={imgSrc1} alt={good.goodsImage} />
      <img src={imgSrc2} alt={good.goodsImage2} />

      <div className="detail-text">
        <p> {good.goodsPrice.toLocaleString()}</p>
        <p>{good.goodsDetail}</p>
      </div>


      {/*   */}
      <div className="detail-sizeSelect">
        <label htmlFor="size-select">  : </label>
        <select id="size-select" value={selectSize} onChange={handleSizeChange}>
          {good.goodsSize.split(",").map((size, index) => (
            <option key={index} value={size}> {size} </option>
          ))}
      </select>
      </div>


      <div className="buttons">
        <button className="basketAdd-button" onClick={addToShoppingBasket}> </button>
        {/*  <button className="basketAdd-button" onClick={() => ShoppingBasket(good, loginMember, userBasketItem, checkLogin)}> </button>*/}
        <button className="back-button" onClick={returnToGoodsShop} > </button>
      </div>
    </div>
  );
};

export default GoodsDetail;