import React, { useEffect, useState } from "react";
import axios from 'axios';
import { useLocation, useNavigate } from "react-router-dom";
import '../css/NaverSignup.css'

function NaverSignup() {
    const [userInfo, setUserInfo] = useState(null);
    const [loading, setLoading] = useState(true);
    const [member, setMember] = useState({
        memberId: '',
        memberPw: '',
        memberPhone: '',
        memberAddress: '',
        memberbirth: ''
    });
    const [errors, setErrors] = useState({
        memberId: '',
        memberPw: '',
        memberName: '',
        memberPhone: '',
    });
    const [emailCheck, setEmailCheck] = useState(false);
    const navigate = useNavigate();
    const location = useLocation();

    useEffect(() => {
        const params = new URLSearchParams(location.search);
        const accessToken = params.get('access_token');
        console.log("  : " + accessToken);

        if (accessToken) {
            axios.get(`/signup/naver?access_token=${accessToken}`)
                .then(response => {
                    setUserInfo(response.data);
                    setLoading(false);
                })
                .catch(err => {
                    console.log("userInfo : ", userInfo)
                    alert("  .");
                });
        }
    }, [location.search]);

    useEffect(() => {
        if (userInfo && userInfo.response) {
            const email = userInfo.response.email;
            axios.post("/emailCheck", null, {
                params: { email }
            })
                .then(response => {
                    if (Number(response.data) !== 0) {
                        setEmailCheck(false);
                        navigate("/login");
                        alert("  .");
                        return null;
                    } else {
                        setEmailCheck(true);
                    }
                })
                .catch(err => {
                    console.error("Error:", err);
                    alert("     .");
                });
        }
    }, [userInfo, navigate]);

    if (loading) {
        return <div>   ...</div>
    }

    const handleChange = (e) => {
        const { name, value } = e.target;

        //   
        if (value) {
            validateField(name, value);
        } else {
            setErrors({
                ...errors,
                [name]: '' //      .
            });
        }

        setMember({
            ...member,
            [name]: value
        });
    };

    const validateField = (name, value) => {
        let errorMsg = '';

        if (name === 'memberId') {
            if (!/^[a-zA-Z0-9]{4,12}$/.test(value)) {
                errorMsg = ' 4-12 ,   .';
            }
        } else if (name === 'memberPw') {
            if (!/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/.test(value)) {
                errorMsg = '  8,       .';
            }
        } else if (name === 'memberName') {
            if (!/^[-a-zA-Z\s]{2,30}$/.test(value)) {
                errorMsg = ' 2-30    .';
            }
        } else if (name === 'memberPhone') {
            if (!/^\d{10,11}$/.test(value)) {
                errorMsg = ' 10-11  .';
            }
        }

        setErrors({
            ...errors,
            [name]: errorMsg
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        //      
        Object.keys(member).forEach((name) => validateField(name, member[name]));

        //     
        if (Object.values(errors).some((error) => error !== '')) {
            console.log('  :', errors);
            return;
        }

        const memberData = {
            memberId: member.memberId,
            memberPw: member.memberPw,
            memberName: userInfo?.response?.name || '', // Safe access
            memberPhone: member.memberPhone,
            memberEmail: userInfo?.response?.email || '', // Safe access
            memberAddress: member.memberAddress,
            memberBirth: member.memberbirth
        };

        axios.post('/naverAPI/register', memberData, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(response => {
            console.log(response.data);
            alert(" .");
            navigate("/login");
        })
        .catch(e => {
            console.error("    : ", e);
            alert(" .");
        })
    };

    return (
        <div className="signup-container">
            {emailCheck && (
                <form onSubmit={handleSubmit}>
                    <div>
                        <label>
                            
                            <div className="input-container">
                                <input
                                    type="text"
                                    name="memberId"
                                    value={member.memberId}
                                    onChange={handleChange}
                                />
                                <button type="button"></button>
                            </div>
                            {errors.memberId && errors.memberId !== '' && (
                                <div style={{ color: 'red' }}>{errors.memberId}</div>
                            )}
                        </label>
                    </div>
                    <div>
                        <label>
                            
                            <input
                                type="password"
                                name="memberPw"
                                value={member.memberPw}
                                onChange={handleChange}
                            />
                            {errors.memberPw && errors.memberPw !== '' && (
                                <div style={{ color: 'red' }}>{errors.memberPw}</div>
                            )}
                        </label>
                    </div>
                    <div>
                        <label>
                            
                            <input
                                type="text"
                                name="memberPhone"
                                value={member.memberPhone}
                                onChange={handleChange}
                            />
                            {errors.memberPhone && errors.memberPhone !== '' && (
                                <div style={{ color: 'red' }}>{errors.memberPhone}</div>
                            )}
                        </label>
                    </div>
                    <div>
                        <label>
                            
                            <input
                                type="text"
                                name="memberAddress"
                                value={member.memberAddress}
                                onChange={handleChange}
                            />
                        </label>
                    </div>
                    <div>
                        <label>
                            
                            <input
                                type="date"
                                name="memberbirth"
                                value={member.memberbirth}
                                onChange={handleChange}
                            />
                        </label>
                    </div>
                    <button type="submit"></button>
                </form>
            )}
        </div>
    );
}

export default NaverSignup;