import React, { useState, useContext } from "react";
import axios from 'axios';
import MyPageContext from '../MyPageContext'; // MyPageContext 
import { useNavigate } from "react-router-dom";
import '../css/BoardPosting.css';

const BoardPosting = () => {
    const { loginMember } = useContext(MyPageContext); //  
    console.log("  ", loginMember);
    const [posting, setPosting] = useState([]);
    const [boardTitle, setBoardTitle] = useState('');
    const [boardContent, setBoardContent] = useState('');
    const navigate = useNavigate();

    // addPost  : boardTitle, boardContent, memberNo    
    const addPost = async (board) => {
        try {
            const res = await axios.post('http://localhost:9000/boards', board);
            setPosting([...posting, res.data]);
        } catch (err) {
            console.error("Error adding post:", err);
        }
    }

    const handleBackClick = () => {
        navigate("/boardMain");
    };
    
    const handleSubmit = (e) => {
        e.preventDefault();
        //    loginMember memberNo   
        const boardData = {
            boardTitle: boardTitle,
            boardContent: boardContent,
            memberNo: loginMember.memberNo
        };
        
        if(!boardTitle && !boardContent) {
            e.preventDefault();
            alert("   .");
        } else {
            alert("  .");
            console.log(" ", boardData);
            addPost(boardData);
            navigate("/boardMain");
        }
    }

    return (
        <div>
            <img className="board-top-img" src="../images/5346ae379c2ae.png"/>
            <div className="board-post-table">
                <button onClick={handleBackClick} className="post-back-button"></button>
                <button onClick={handleSubmit} type="submit" className="post-button">  </button>
                <form className="post-table">
                    <div>
                        <input type="text" className="posting-title" value={boardTitle} placeholder=" ." onChange={(e) => setBoardTitle(e.target.value)} required />
                    </div>
                    <div>
                        <textarea className="posting-content" value={boardContent} placeholder=" ." onChange={(e) => setBoardContent(e.target.value)} required />
                    </div>
                </form>
            </div>
        </div>
    )
}

export default BoardPosting;
