import React, { useEffect } from "react";
import '../css/KakaoMap.css'; // CSS  .
import { defaultFormat } from "moment";
const KakaoMap = () => {
  useEffect(() => {
    const script = document.createElement("script");
    script.async = true;
    script.src = "https://dapi.kakao.com/v2/maps/sdk.js?appkey=0b1640c41caed1fef96fc19812168ffe&autoload=false";
    document.head.appendChild(script);
    script.onload = () => {
      window.kakao.maps.load(() => {
        const mapContainer = document.getElementById("map");
        const mapOption = {
          center: new window.kakao.maps.LatLng(37.5165, 127.0194), //  : 
          level: 5, //   
        };
        const map = new window.kakao.maps.Map(mapContainer, mapOption);
        //      
        const locations = [
          { name: '', lat: 37.509850, lng: 127.033196 },
          { name: '', lat: 37.517362, lng: 127.029288 },
          { name: ' ', lat: 37.527087, lng: 127.029718 },
          { name: ' ', lat: 37.490768, lng: 127.058016 },
          { name: ' ', lat: 37.525140, lng: 127.028702 },
          { name: '', lat: 37.484583, lng: 127.066226 },
          { name: ' ', lat: 37.527455, lng: 127.035286 },
          { name: '', lat: 37.492895, lng: 127.081242 },
          { name: '', lat: 37.522953, lng: 127.049424 },
          { name: ' ', lat: 37.513272, lng: 127.100168 },
          { name: '  ', lat: 37.5025, lng: 127.1258 },
          { name: '', lat: 37.4678, lng: 127.0389 },
          { name: '', lat: 37.5442, lng: 127.0924 },
          { name: '3', lat: 37.5151, lng: 127.0732 },
          { name: '', lat: 37.4673, lng: 127.1276 },
          { name: '', lat: 37.5196, lng: 126.9945 },
          { name: '2', lat: 37.5151, lng: 127.0732 },
          { name: 'FC ', lat: 37.5011, lng: 127.0915 },
          { name: '', lat: 37.4944, lng: 127.1182 },
          { name: '', lat: 37.5444, lng: 127.0565 }
        ];
        locations.forEach((location) => {
          const markerPosition = new window.kakao.maps.LatLng(
            location.lat,
            location.lng
          );
          //  
          const marker = new window.kakao.maps.Marker({
            position: markerPosition,
            title: location.name,
          });
          marker.setMap(map);
          //       
          const infowindow = new window.kakao.maps.InfoWindow({
            content: `<div style="padding:5px;font-size:12px;">${location.name}</div>`,
          });
          //     
          window.kakao.maps.event.addListener(marker, 'mouseover', () => {
            infowindow.open(map, marker);
          });
          //     
          window.kakao.maps.event.addListener(marker, 'mouseout', () => {
            infowindow.close();
          });
        });
        //     
        const moveToCurrentLocation = () => {
          if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition((position) => {
              const currentPos = new window.kakao.maps.LatLng(
                position.coords.latitude,
                position.coords.longitude
              );
              //    
              const currentMarker = new window.kakao.maps.Marker({
                position: currentPos,
                title: " ",
                map: map,
                zIndex: 10 //     
              });
              //     
              map.setCenter(currentPos);
            }, (error) => {
              console.error("Geolocation Error: ", error);
              alert("    .");
            });
          } else {
            alert("Geolocation   .");
          }
        };
        //     
        const locateButton = document.getElementById("locate-button");
        locateButton.addEventListener("click", moveToCurrentLocation);
      });
    };
  }, []);
  return (
    <div className="map-container">
      <div id="map" />
      <div className="map-control">
        <button id="locate-button"></button> {/*   */}
      </div>
    </div>
  );
};

export default KakaoMap;