import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../css/FindLogin.css";
import axios from "axios";
import Modal from "../Layout/Modal";
import PasswordChange from "./PasswordChange";

const FindPw = () => {
  const [memberId, setMemberId] = useState("");
  const [memberName, setMemberName] = useState("");
  const [memberPhone, setMemberPhone] = useState("");
  const [operationKey, setOperationKey] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [securityCode, setSecurityCode] = useState("");
  const [userInfo, setUserInfo] = useState(null);
  const [change, setChange] = useState(false);
  
  const [isModalOpen, setIsModalOpen] = useState(false);

  const findResult = async () => {
    if (!memberId || !memberName || !memberPhone) {
      alert(" !");
      return;
    }
    try {
      const response = await axios.post(
        "/findPw",
        {
          memberId,
          memberName,
          memberPhone,
        },
        {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        }
      );
      if (response.data) {
        setUserInfo(response.data);
        alert("   .");
        setErrorMessage("");
        setChange(true);
      } else {
        setErrorMessage("  .  ");
      }
    } catch (error) {
      console.error("Axios error:", error);
      alert("  .");
    }
  };

  const sendCode = async () => {
    try {
      const params = new URLSearchParams();
      params.append('email', userInfo.memberEmail);

      const response = await axios.post('/auth/send-code', params.toString(), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      });

      if (response.data.status === 'success') {
        alert('  .');
        setOperationKey(true);
      } else {
        alert('   .');
      }
    } catch (error) {
      console.error('Axios error:', error);
      alert('Axios error: ' + error.message);
    }
  };

  const submitSuccess = async () => {
    if (!securityCode) {
      alert("   .");
      return;
    }

    try {
      const response = await axios.post(
        "/auth/verify-code",
        new URLSearchParams({
          email: userInfo.memberEmail,
          code: securityCode,
        }).toString(),
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
        }
      );

      if (response.data.status === "success") {
        alert(" .");
        setIsModalOpen(true);
      } else {
        alert("   .");
      }
    } catch (error) {
      console.error("Axios error:", error);
      alert("   .");
    }
  };

  const closeModal = () => setIsModalOpen(false);

  return (
    <div className="findIdPw-container">
      <div className="find-Id-Pw">
        <Link to="/findId" className="Link-list">
           
        </Link>
        <Link to="/findPw" className="Link-list">
           
        </Link>
      </div>

      <div className="findPw-container">
        <div className="findPw-title">
          <h1> </h1>
        </div>
        {!change ? (
          <div className="findPw">
              <div>
                <input
                  type="text"
                  placeholder=" ."
                  value={memberId}
                  onChange={(e) => setMemberId(e.target.value)}
                  required
                />
              </div>
              <div>
                <input
                  type="text"
                  placeholder=" ."
                  value={memberName}
                  onChange={(e) => setMemberName(e.target.value)}
                  required
                />
              </div>
              <div>
                <input
                  type="text"
                  placeholder="-   11 ."
                  value={memberPhone}
                  onChange={(e) => setMemberPhone(e.target.value)}
                  required
                />
              </div>
              {errorMessage && (
              <div className="error-message">
                <p>{errorMessage}</p>
              </div>
            )}
            <div className="findPw-button">
              <button onClick={findResult}>    &nbsp;  </button>
            </div>
 
            <ul className="login-list">
              <li className="login-item">
                <Link to="/login" className="login-link">
                  <img src="soccer.jpg" />
                  &nbsp; 
                </Link>
              </li>
              <li className="login-item">
                <Link to="/register" className="login-link">
                  <img src="soccer.jpg" />
                  &nbsp;
                </Link>
              </li>
            </ul>
          </div>
          
        ) : (
            <div className="findPw">
              <div className="email-auth">
              <div className="email-Auth-content">
                <h4>   <br/>.</h4>
                <div className="email-Auth">
                  <p>"{userInfo.memberEmail}"</p>
                </div>
              </div>
              {!operationKey ? (
                <div className="email-Auth">
                  <button className="btn btn-dark" onClick={sendCode}>
                     
                  </button>
                </div>
              ) : (
                <div>
                  <input
                    type="text"
                    value={securityCode}
                    onChange={(e) => setSecurityCode(e.target.value)}
                    placeholder=" ."
                  />
                  <br />
                  <button className="btn btn-dark" onClick={submitSuccess}> </button>
                </div>
              )}
              </div>
              <ul className="login-list">
              <li className="login-item">
                <Link to="/login" className="login-link">
                  <img src="soccer.jpg" />
                  &nbsp; 
                </Link>
              </li>
              <li className="login-item">
                <Link to="/register" className="login-link">
                  <img src="soccer.jpg" />
                  &nbsp;
                </Link>
              </li>
            </ul>
            </div>
        )}
      </div>

      {/*   */}
      <Modal isOpen={isModalOpen} onClose={closeModal}>
        <PasswordChange userInfo={userInfo} />
      </Modal>
    </div>
  );
};

export default FindPw;